class CustomMenu extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.handleChange = this.handleChange.bind(this);

    this.state = { value: '' };
  }

  handleChange(e) {
    this.setState({ value: e.target.value.toLowerCase().trim() });
  }

  render() {
    const {
      children,
      style,
      className,
      'aria-labelledby': labeledBy,
    } = this.props;

    const { value } = this.state;

    return (
	      <div style={style} aria-labelledby={labeledBy}>
	      	<Container>
	      		<Row>
			        <FormControl
			          className="mx-3 my-2 w-auto align-self-center"
			          placeholder="Type to filter..."
			          onChange={this.handleChange}
			          value={value}
			        />
	        	</Row>
	        
				<Row>
					<Col>
				        <ul className="list-unstyled">
				          {React.Children.toArray(children).slice(0, 6).filter(
				            child => !value || child.props.children.toLowerCase().startsWith(value),
				          )}
				        </ul>
				    </Col>
				    <Col>
				        <ul className="list-unstyled">
				          {React.Children.toArray(children).slice(6, 12).filter(
				            child => !value || child.props.children.toLowerCase().startsWith(value),
				          )}
				        </ul>
				    </Col>
				    <Col>
				        <ul className="list-unstyled">
				          {React.Children.toArray(children).slice(12, 18).filter(
				            child => !value || child.props.children.toLowerCase().startsWith(value),
				          )}
				        </ul>
				    </Col>
				    <Col>
				        <ul className="list-unstyled">
				          {React.Children.toArray(children).slice(18, 24).filter(
				            child => !value || child.props.children.toLowerCase().startsWith(value),
				          )}
				        </ul>
				    </Col>
				</Row>

				

				
		    
		    </Container>
	      </div>
	    
    );
  }
}

class CustomItems extends React.Component {
	render() {
		return (
			<Dropdown.Menu as={CustomMenu}>
				{genre.map((type, i) => 
					<Dropdown.Item 
						key={type}
						eventKey={type}
						as={Link}
						to={'/' + type}
						onClick={() => this.props.onClick()}
					>
					{type}
					</Dropdown.Item>
				)}
			</Dropdown.Menu>
		);
	}
}

class NavLink extends React.Component {
	render() {
		return (
			<Nav.Item>
                <Nav.Link
                active={this.props.isActive ? true: false}
                className="nav-link" 
                as={Link}
                to={this.props.path}
                onClick={() => this.props.onClick()}>
                {this.props.text}
                </Nav.Link>
        	</Nav.Item>
		);
	}
}




class Header extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			links: [
				{path: "/", text: "Home", isActive: true},
				{path: "/1", text: "Page1", isActive: false},
				{path: "/2", text: "Page2", isActive: false}
			]
		}
		this.handleClick = this.handleClick.bind(this);
	}

	handleClick(i) {
		const links = this.state.links.slice();
		for (const j in links) {
			links[j].isActive = i == j;
		}
		this.setState({links: links});
	}

	render() {
		return(
			<div>
			    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" fixed="top">
				  <Navbar.Brand as={Link} to="/">SayYourMovie</Navbar.Brand>
				  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
				  <Navbar.Collapse id="responsive-navbar-nav">
				  	<Form inline>
				      <FormControl type="text" placeholder="Search" className="mr-sm-2" />
				      <Button variant="outline-info">Search</Button>
				    </Form>
				    <Nav fill className="mr-auto" style={{width:"20%"}}>
				      {this.state.links.map((link, i) => 
			      		<NavLink
			      			key={link.text}
			      			path={link.path}
			      			text={link.text}
			      			isActive={link.isActive}
			      			onClick={() => this.handleClick(i)}
			      		/>
				      )}
			          <Nav.Item>
			          	<NavDropdown title="Movies" className="dropdown full-width" id="dropdown-menu">
						    <CustomItems onClick={() => this.handleClick(-1)}/>
				      	</NavDropdown>
			          </Nav.Item>
				    </Nav>
				    <Nav>
				    	<Navbar.Text>
						  	Hi
						</Navbar.Text>
						  <Nav.Link className="nav-link text-white" href="#">
							Signin / Signup
						</Nav.Link>
				    </Nav>
				  </Navbar.Collapse>
				</Navbar>
			</div>
		);
	}
}

