import React from 'react';
import { Link } from 'react-router-dom';
import "../css/LinkStyle.css"
// Bootstrap
import { Navbar, Container, Row, Col, Card, Carousel} from 'react-bootstrap';



class CardSliderCol extends React.Component {
	render() {
		return (
			<Col md={2}>
	  			<figure className="figure">
                      <img src="https://tupian.tupianzy.com/pic/upload/vod/2019-01-16/201901161547644844.jpg" className="figure-img img-fluid rounded" alt="..." />
                      <figcaption className="figure-caption text-right">Spider Man</figcaption>
                    </figure>
	  		</Col>
		);
	}
}

class CardSliderRow extends React.Component {
	render() {
		return (
			<Row>
		  		<CardSliderCol />
		  		<CardSliderCol />
		  		<CardSliderCol />
		  		<CardSliderCol />
				<CardSliderCol />
		  		<CardSliderCol />
		  	</Row>
		);
	}
}

class CardSlider extends React.Component {
	render() {
		return (
			<Carousel indicators={false} interval={999999}>
			  <Carousel.Item>
			  	<CardSliderRow />
			  </Carousel.Item>
			  <Carousel.Item>
			  	<CardSliderRow />
			  </Carousel.Item>
			</Carousel>
		);
	}
}

class RecMovieCard extends React.Component {
	render() {
		return (
			<Card>
			  <Card.Header className="shadow-lg bg-light">
			  	<Navbar.Brand as={Link} to={"/" + this.props.series}>
                	{this.props.series}
                </Navbar.Brand>
              </Card.Header>
			  <Card.Body>
			  	<CardSlider />
			  </Card.Body>
			</Card>
		);
	}
}

class RecContainer extends React.Component {
	render() {
		return (
			<Container style={{maxWidth: 1500}} >
			  <Row className="shadow-lg">
			  	<RecMovieCard series="Recommend Movies"/>
			  </Row>
			  {genre.map((type) => 
			  	<Row className="shadow-lg" style={{marginTop:10}}>
				  	<RecMovieCard series={type} />
				</Row>
			  )}
			</Container>
		);
	}	
}

